import * as React from "react";
import { COLORS } from "../colors.const";
import { PageProps } from "components/Page/Page.props";

import { Layout } from "components/layout";
import Seo from "components/seo";

import {
  Slide, Props as SlideProps 
} from "../components/Slide/Slide";

import {
  Main,
  ProgrammingLanguages,
  Overview,
  News,
  Partners,
  MasterClass,
  Blog,
} from '../views/index';

import { getClassName } from "../tools/getClassName";

import './index.scss';
import { Page } from "components/Page/Page";

const cn = getClassName('index');

const Content = [
  {
    slide: { className: cn('slide', { type: 'main' }) },
    content: <Main />,
  },
  {
    slide: {
      id: 'overview',
      flexible: true,
      className: cn('slide') 
    },
    content: <ProgrammingLanguages />,
  },
  {
    slide: {
      flexible: true,
      className: cn('slide') 
    },
    content: <Overview />
  },
  {
    slide: {
      flexible: true,
      className: cn('slide', { type: 'partners' }) 
    },
    content: <Partners />
  },
  // {
  //   slide: {
  //     backgroundColor: COLORS.MIGOL_BLUE,
  //     className: cn('slide', { type: 'master-class' }) 
  //   },
  //   content: <MasterClass />
  // },
  {
    slide: {
      flexible: true,
      className: cn('slide') 
    },
    content: <News />
  },
  {
    slide: {
      id: 'about',
      flexible: true,
      className: cn('slide', { type: 'blog' }) 
    },
    content: <Blog />
  },
] as Array<{
  slide: SlideProps,
  content: React.ReactNode
}>;


const IndexPage = (props: PageProps) => {  
  return (
    <Page {...props}>
      <Layout page={props} >
        {Content.map(({ slide, content }, index) => (
          <Slide key={`slide-${index}`} {...slide}>
            {content}
          </Slide>
        ))}
      </Layout>
    </Page>
  );

};

export const Head = () => (<>
  <Seo title="Home" description={undefined} />
</>);

export default IndexPage;
