import React from 'react';
import { getClassName } from 'tools/getClassName';
import { cloneElement } from 'tools/cloneElement';
import { Text } from 'components/Text/Text';

export type Props = {
  title: string;
  subtitle: string;
  ImageNode: React.ReactNode;
  className?: string;
  interactive?: boolean;
};

const cn = getClassName('card');

import './Card.scss';

export const Card: React.FC<Props> = ({
  title,
  subtitle,
  ImageNode,
  className,
  interactive = true,
}) => {
  return (
    <div className={cn({ interactive }, className)}>
      <div className={cn('image-wrapper')}>
        {cloneElement(ImageNode, { className: cn('image') })}
      </div>
      <div className={cn('content')}>
        {title && <Text color='grey' className={cn('title')} capitilize>{title}</Text>}
        <Text color='light-grey' className={cn('subtitle')} capitilize>{subtitle}</Text>
      </div>
    </div>
  );
};

